				"html": "<p><code>Regular expressions</code> are used to find certain words or patterns inside of strings.</p>\n<p>Regular expressions are usually surrounded by <code>/</code> symbols.</p>\n<p>For example, if we wanted to find the number of times the word the occurred in the string The dog chased the cat, we could use the following regular expression: <code>/the+/gi</code></p>\n<p>Let's break this down a bit:</p>\n<p>the is the pattern we want to match.</p>\n<ul>\n<li><code>+</code> means we want to find one or more occurrences of this pattern.</li>\n<li><code>g</code> means that we want to search the entire string for this pattern.</li>\n<li><code>i</code> means that we want to ignore the case (uppercase or lowercase) when searching for the pattern.</li>\n</ul>",
